      { name: '烈嶼鄉', zip: '894' },
      { name: '烏坵', zip: '896' },
    ],
  },
  {
    name: '連江縣',
    areas: [
      { name: '南竿', zip: '209' },
      { name: '北竿', zip: '210' },
      { name: '莒光', zip: '211' },
      { name: '東引', zip: '212' },
    ],
  },
];
import selectComponent from '@/components/selectComponent.vue'

let towns = function() { 
    return cities.map( function(item) {
       return item.name
    }) //重整抓出array 中的name值
}
let areas = function() {
  return cities[this.townIndex].areas.map(
      function(item){
          return item.name
      }) //重整抓出array 中的area.name值
}

let zip = function() {
  return cities[this.townIndex].areas[this.areaIndex].zip
}

let townIndex = function() {
  this.areaIndex = 0
} //當 townIndex 變化時，同時對 areaIndex reset 為 0，所以要使用 watch。

export default {
  name: 'App',
  components: {
    selectComponent
  },
  data: () => ({
    townIndex: 0 ,
    areaIndex: 0,
  }),
  computed: {
    towns,
    areas,
    zip,
  },
  watch: {
    townIndex,
  }
}
</script>
